import React from 'react';


export default class App extends React.Component{
  //  
  state ={
    count: 0,
    isCounting: false
  };

//    isCounting   true,  
//  setInterval    count  1
handelStart = () => {
  this.setState({isCounting: true});
  this.couterId = setInterval(() => {
    this.setState({ count: this.state.count + 1 });
  }, 1000);
};

//    isCounting   false,  
handelStop = () => {
  this.setState({ isCounting: false });
//   clearInterval  couterId  handelStart
  clearInterval(this.couterId);
};

//       
handelReset = () => {
  this.setState({isCounting: false, count: 0 });
  clearInterval(this.couterId);
};


//    localStorage
componentDidUpdate() {
  localStorage.setItem('timer', this.state.count)
}

//    localStorage
componentDidMount() {
  const userCount = localStorage.getItem('timer');
  //    count  
  // +userCount - +    
  if (userCount) {
    this.setState({ count: +userCount });
  }
}



//         ,  
componentWillUnmount() {
  clearInterval(this.counterId);
}


render() {
  return (
    <div className="App">
    <h1>Timer</h1>
    <h3>{this.state.count}</h3>
    {/*      Start   Stop */}
    {!this.state.isCounting ? (
      <button onClick={this.handelStart}>Start</button>
    ) : (
      <button onClick={this.handelStop}>Stop</button>
    )}
      <button onClick={this.handelReset}>Reset</button>
    </div>
    );
 }
}


